html {

    scroll-behavior: smooth;
}

.App {
    text-align: center;
}

.App-logo {
    height: 40vmin;
    pointer-events: none;
}
@media (prefers-reduced-motion: no-preference) {
    .App-logo {
        animation: App-logo-spin infinite 20s linear;
    }
}

/* dim image in app-header*/
.App-header {
    background-image: url("newBasket.jpg");
    background-size: cover;
    /*default for entire container*/
    background-position: center;
    background-repeat: no-repeat;
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
    filter: brightness(92%);
}
@media (min-width: 50%) {
    .App-header,
    image-header {
        background-size: cover;
    }
}

.image-header {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 70vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    color: white;
}

.App-link {
    color: 'black';
}

.navBar {
    display: flex;
    background-color: rgba(0, 0, 0, 0.5);
    align-items: center;
    border-radius: 10px;
    justify-content: space-between;
    position: absolute;
    top: 20px;
    padding: 15px;
    z-index: 1;
    color: white;
    width: 75%;
    border-radius: 10px;
    margin-top: -10px;
}

/*Title on the left side of the Nav Bar*/
.nav-text {
    font-size: 24px;
    font-weight: bold;
    color: white;
}

.navBar a {
    font-weight: bold;
    color: black;
    padding: 10px 20px;
    border-radius: 10px;
    background-color: white;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    text-decoration: none;
}

